.Header {
  display: flex;
  flex-direction: row;
  justify-content: start;
  position: fixed;
  padding: 20px ;
}
.menu-list {
  ul {
    list-style-type: none;
    display: block;
    li {
      display: inline-block;
      padding-right: 20px;
      font-weight: bold;
      cursor: pointer;
    }
  }
  .sub-list{
    position: absolute;
    display: flex;
    flex-direction: column;
    background-color: #ffff;
    padding: 20px;
    margin-top: 10px;
    line-height: 2;
    li{
      border-bottom: 1px solid #eeeeee;
      font-weight: normal;
     
    }
  }
}

.banner{
  background-image: url("../asset/img-banner.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  height: 700px;
}
// Product list
.container {
  width: 96%;
  margin: 0 auto;
}
.Product-lists {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
  margin: 40px 0;

  .product-item {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    border: 2px solid #eeeeee;
    width: 26%;
    padding: 20px;
    min-height: 0;
    margin-bottom: 30px;
  
    .content-left {
      img {
        max-width: 150px;
      }
    }
    .content_right {
      h5 {
        font-size: 1.4rem;
        line-height: 1.5;
        margin: 20px 0 5px;
      }
      .price {
        font-size: 1.5rem;
        color: #222222;
        font-weight: bold;
        .current-price {
          color: red;
          display: inline-block;
          padding-left: 20px;
        }
        .price-edit {
          text-decoration: line-through;
          color: #bbbbbb;
        }
      }
    }
  }
}

